/* burger bun */
.menu .burger-bun {
    display: block;
}
.menu .burger-bun span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #ffffff;
    border-radius: 3px;
    z-index: 1;
    transform-origin: 4px 0px;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
.menu .burger-bun span:nth-last-child(3) {
    margin-top: 0.425rem;
}
.menu .burger-bun span:nth-last-child(1) {
    transform-origin: 0% 100%;
}

.js .menu[data-open=true] .burger-bun span {
    opacity: 1;
    transform: rotate(45deg) translate(7px, -2px);
    background: #ffffff;
}
.js .menu[data-open=true] .burger-bun span:nth-last-child(2) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
}
.js .menu[data-open=true] .burger-bun span:nth-last-child(1) {
    transform: rotate(-45deg) translate(1px, 8px);
}

.no-js .menu .burger-bun input:checked ~ span {
    opacity: 1;
    transform: rotate(45deg) translate(7px, -2px);
    background: #ffffff;
}
.no-js .menu .burger-bun input:checked ~ span:nth-last-child(2) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
}
.no-js .menu .burger-bun input:checked ~ span:nth-last-child(1) {
    transform: rotate(-45deg) translate(1px, 8px);
}

